import { firestore } from '@/plugins/firebase'

export default {
  namespaced: true,

  state: {
    list: [],
    likes: [],
    tags: []
  },

  getters: {
    getList: s => s.list,
    getProjects: s => s.list.filter(item => item.project),
    getLikes: s => s.likes,
    getTags: s => s.tags
  },

  mutations: {
    setList (state, payload) {
      state.list = payload
    },
    setTags (state, payload) {
      state.tags = [...new Set(payload.reduce((res, item) => res.concat(item.tags), []))]
    },
    setLikes (state, payload) {
      state.likes = payload
    },

    addLike (state, payload) {
      const index = state.list.findIndex(item => item.id === payload)

      if (~index) {
        state.list[index].likes++
        state.likes.push(payload)
        localStorage.setItem('likes', JSON.stringify(state.likes))
      }
    },
    delLike (state, payload) {
      const index = state.list.findIndex(item => item.id === payload)
      
      if (~index) {
        if (state.list[index].likes > 0) {
          state.list[index].likes--
        }

        state.likes = state.likes.filter(item => item !== payload)
        localStorage.setItem('likes', JSON.stringify(state.likes))
      }
    },

    addView (state, payload) {
      const index = state.list.findIndex(item => item.id === payload)

      if (~index) {
        state.list[index].views++
      }
    },

    setShare (state, payload) {
      state.share = payload
    },

    // addShare (state, payload) {
    //   const index = state.list.findIndex(item => item.id === payload)

    //   if (~index) {
    //     state.list[index].share++
    //   }
    // }
  },

  actions: {
    async setList ({ commit }) {
      try {
        const res = await firestore.collection('works').get()
        const list = []
        
        res.forEach(item => {
          list.push({ id: item.id, ...item.data() })
        })

        commit('setList', list)

        try {
          commit('setLikes', JSON.parse(localStorage.getItem('likes')) || [])
          commit('setTags', list)
        }
        catch {
          throw 'Не удалось получить список лайков'
        }
      }
      catch {
        throw 'Не удалось получить список работ'
      }
    },

    addLike ({ commit, state }, payload) {
      const item = state.list.find(item => item.id === payload)

      if (item) {
        firestore.collection('works').doc(payload).update({
          likes: item.likes + 1
        })
        .catch(() => {})

        commit('addLike', payload)
      }
    },
  
    delLike ({ commit, state }, payload) {
      const item = state.list.find(item => item.id === payload)

      if (item && item.likes > 0) {
        firestore.collection('works').doc(payload).update({
          likes: item.likes - 1
        })
        .catch(() => {})
        
        commit('delLike', payload)
      }
    },

    addView ({ commit, state }, payload) {
      const item = state.list.find(item => item.id === payload)

      if (item) {
        firestore.collection('works').doc(payload).update({
          views: item.views + 1
        })
        .catch(() => {})

        commit('addView', payload)
      }
    },

    // addShare ({ commit, state }, payload) {
    //   const item = state.list.find(item => item.id === payload)

    //   if (item) {
    //     firestore.collection('works').doc(payload).update({
    //       share: item.share + 1
    //     })
    //     .catch(() => {})

    //     commit('addShare', payload)
    //   }
    // }
  }
}