import Vue from 'vue'
import Vuex from 'vuex'
import account from './modules/account'
import worklist from './modules/worklist'
import search from './modules/search'
import sort from './modules/sort'

Vue.use(Vuex)

export default store => {
  return new Vuex.Store({
    store,
    modules: {
      account,
      worklist,
      search,
      sort
    }
  })
}
