<template>
  <component id="app" :is="getLayout" />
</template>

<script>
export default {
  name: 'app',

  data: () => ({
    layout: null
  }),

  watch: {
    $route: {
      immediate: true,
      handler (v) {
        if (v.name === 'page-404') {
          return this.layout = 'error'
        }

        const layout = v.matched[0].components.default.layout

        if (layout) {
          this.layout = layout
        }
        else {
          this.layout = 'default'
        }
      }
    }
  },

  computed: {
    getLayout () {
      if (this.layout) {
        return () => import(`@/layouts/${this.layout}/Layout`)
      }

      return {}
    }
  }
}
</script>