export default {
  namespaced: true,
  
  state: {
    query: '',
    history: [],
    tags: []
  },

  getters: {
    getQuery: s => s.query,
    getTags: s => s.tags,
    getHistory: s => s.history
  },

  mutations: {
    setQuery (state, payload) {
      state.query = payload
    },
    setHistory (state, payload) {
      if (!state.history.find(item => item === payload)) {
        state.history.push(payload)
      }
    },
    delHistory (state, payload) {
      state.history.find((item, index) => {
        if (item === payload) {
          state.history.splice(index, 1)

          return true
        }
      })
    },
    cleanHistory (state) {
      state.history = []
    },

    addTag (state, payload) {
      state.tags.push(payload)
    },
    delTag (state, payload) {
      state.tags.find((item, index) => {
        if (item === payload) {
          state.tags.splice(index, 1)

          return true
        }
      })
    }
  },

  actions: {
    submit ({ commit }, payload) {
      commit('setQuery', payload)

      if (payload.length) {
        commit('setHistory', payload)
      }
    }
  }
}