import Vue from 'vue'
import App from '@/components/App.vue'
import createRouter from '@/plugins/router'
import createStore from '@/plugins/store'
import '@/plugins'

export default () => {
  const router = createRouter()
  const store = createStore()

  const app = new Vue({
    router,
    store,
    render: h => h(App)
  })

  return { app, router, store }
}