export default {
  namespaced: true,

  state: {
    active: 0,
    order: false,
    list: [
      {
        label: 'По дате',
        value: 'date'
      },
      {
        label: 'Лучшее',
        value: 'likes'
      },
      {
        label: 'Просмотры',
        value: 'views'
      },
      {
        label: 'По порядку',
        value: 'order'
      }
    ]
  },

  getters: {
    getList: s => s.list,
    getActive: s => s.list[s.active] || s.list[0],
    getOrder: s => s.order,

    getSorted (state, getters) {
      return list => {
        return list.slice().sort((a, b) => {
          if (Number(a[getters.getActive.value])) {
            if (state.order) {
              return a[getters.getActive.value] - b[getters.getActive.value]
            }

            return b[getters.getActive.value] - a[getters.getActive.value]
          }

          if (state.order) {
            return a.title.localeCompare(b.title)
          }

          return b.title.localeCompare(a.title)
        })
      }
    }
  },

  mutations: {
    set (state, payload) {
      if (payload === state.active) {
        state.order = !state.order
      }

      state.active = payload
    }
  }
}