import { firestore } from '@/plugins/firebase'

export default {
  namespaced: true,

  state: {
    data: {}
  },

  getters: {
    get: s => s.data
  },

  mutations: {
    set (state, payload) {
      state.data = payload
    }
  },

  actions: {
    async set ({ commit }, payload) {
      try {
        const res = await firestore.collection('account').doc(payload).get()

        if (res.exists) {
          commit('set', res.data())
        } else {
          throw 'Профиль не найден'
        }
      }
      catch {
        throw 'Ошибка при загрузке профиля'
      }
    }
  }
}